.theme-toggle {
  position: fixed;
  top: 1rem;
  right: 1rem;
  z-index: 1000;
}

.theme-toggle-container {
  display: flex;
  background-color: var(--cream);
  border: 2px solid var(--latte);
  border-radius: 12px;
  padding: 0.25rem;
  box-shadow: 0 4px 12px rgba(139, 115, 85, 0.15);
  backdrop-filter: blur(10px);
}

.theme-btn {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: none;
  border: none;
  border-radius: 8px;
  padding: 0.5rem 0.75rem;
  cursor: pointer;
  transition: all 0.3s ease;
  color: var(--coffee-brown);
  font-family: 'Inter', sans-serif;
  font-size: 0.8rem;
  font-weight: 500;
  min-width: 50px;
}

.theme-btn:hover {
  background-color: var(--steam);
  transform: translateY(-1px);
}

.theme-btn.active {
  background-color: var(--latte);
  color: var(--coffee-brown);
  box-shadow: 0 2px 8px rgba(139, 115, 85, 0.2);
}

.theme-icon {
  font-size: 1.2rem;
  margin-bottom: 0.25rem;
  transition: transform 0.3s ease;
}

.theme-btn:hover .theme-icon {
  transform: scale(1.1);
}

.theme-label {
  font-size: 0.7rem;
  font-weight: 600;
  letter-spacing: 0.5px;
}

/* Responsive design */
@media (max-width: 768px) {
  .theme-toggle {
    top: 0.5rem;
    right: 0.5rem;
  }
  
  .theme-toggle-container {
    padding: 0.2rem;
  }
  
  .theme-btn {
    padding: 0.4rem 0.6rem;
    min-width: 45px;
  }
  
  .theme-icon {
    font-size: 1rem;
  }
  
  .theme-label {
    font-size: 0.65rem;
  }
} 