* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #F5F5DC; /* Flat white background */
  color: #2C1810; /* Dark coffee brown text */
  line-height: 1.6;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Coffee flat-white theme colors (light mode) */
:root {
  --flat-white: #F5F5DC;
  --coffee-brown: #2C1810;
  --latte: #E6D7C3;
  --espresso: #1A0F0A;
  --cappuccino: #D4C4A8;
  --mocha: #8B7355;
  --cream: #FDFBF7;
  --steam: #F8F6F0;
  
  /* Enhanced coffee theme colors */
  --coffee-bean: #3E2723;
  --caramel: #D2691E;
  --vanilla: #F3E5AB;
  --cinnamon: #D2691E;
  --hazelnut: #B8860B;
  --dark-roast: #1A0F0A;
  --light-roast: #F8F6F0;
  --coffee-grounds: #2C1810;
}

/* Dark coffee theme colors */
[data-theme="dark"] {
  --flat-white: #1A0F0A;
  --coffee-brown: #E6D7C3;
  --latte: #2C1810;
  --espresso: #F5F5DC;
  --cappuccino: #3E2723;
  --mocha: #D4C4A8;
  --cream: #2C1810;
  --steam: #1A0F0A;
  
  /* Enhanced dark coffee theme colors */
  --coffee-bean: #F3E5AB;
  --caramel: #D2691E;
  --vanilla: #3E2723;
  --cinnamon: #D2691E;
  --hazelnut: #D4C4A8;
  --dark-roast: #F5F5DC;
  --light-roast: #1A0F0A;
  --coffee-grounds: #E6D7C3;
}

/* System preference detection */
@media (prefers-color-scheme: dark) {
  :root:not([data-theme="light"]) {
    --flat-white: #1A0F0A;
    --coffee-brown: #E6D7C3;
    --latte: #2C1810;
    --espresso: #F5F5DC;
    --cappuccino: #3E2723;
    --mocha: #D4C4A8;
    --cream: #2C1810;
    --steam: #1A0F0A;
    
    --coffee-bean: #F3E5AB;
    --caramel: #D2691E;
    --vanilla: #3E2723;
    --cinnamon: #D2691E;
    --hazelnut: #D4C4A8;
    --dark-roast: #F5F5DC;
    --light-roast: #1A0F0A;
    --coffee-grounds: #E6D7C3;
  }
} 